html {
  font-family: 'Open Sans';
  font-size: 14px; }

h1 {
  margin: 0; }

ul {
  margin: 0;
  padding-left: 14px; }

li:not(:last-child) {
  padding-bottom: 4px; }

input,
textarea {
  box-sizing: border-box;
  padding: 4px; }

select {
  padding: 3px; }

.m-xl {
  margin: 20px; }

.m-t-md {
  margin-top: 12px; }

.m-t-xl {
  margin-top: 20px; }

.m-t-xxl {
  margin-top: 40px; }

.m-b-md {
  margin-bottom: 12px; }

.m-b-xl {
  margin-bottom: 20px; }

.p-xl {
  padding: 20px; }

.p-l-xl {
  padding-left: 20px; }

.p-r-xl {
  padding-right: 20px; }

.p-b-xs {
  padding-bottom: 4px; }

.text-center {
  text-align: center; }

.color-white {
  color: #fff; }

.text-bold {
  font-weight: bold; }

.display-block {
  display: block; }

.flex-space-between {
  align-items: flex-end;
  display: flex;
  justify-content: space-between; }

.bot-container {
  display: grid;
  grid-column-gap: 2rem;
  grid-row-gap: 2rem;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr; }
  .bot-container .bot-card {
    border: 1px solid #ddd;
    box-shadow: 0 2px 5px 1px #ddd; }
  .bot-container .bot-card-header {
    align-items: center;
    background-color: #555;
    border-bottom: #aaa;
    color: #fff;
    display: flex;
    justify-content: space-between;
    padding: 5px 10px; }
    .bot-container .bot-card-header .bot-name {
      font-size: 1.4rem;
      padding: 2px 0; }
    .bot-container .bot-card-header .bot-realm-name {
      font-size: 80%;
      padding: 4px 0 0 2px;
      text-transform: uppercase; }
    .bot-container .bot-card-header .bot-class-icon {
      position: relative; }
      .bot-container .bot-card-header .bot-class-icon img {
        height: 50px;
        width: 50px; }
    .bot-container .bot-card-header .bot-level {
      background-color: #fff;
      border: 1px solid #000;
      border-radius: 10px;
      bottom: 0;
      color: #000;
      font-size: 80%;
      height: 20px;
      line-height: 20px;
      position: absolute;
      right: 0;
      text-align: center;
      width: 20px; }
  .bot-container .bot-card-body {
    padding: 5px; }
    .bot-container .bot-card-body.inactive {
      background-color: #eee; }
    .bot-container .bot-card-body.active {
      background-color: #e6f3ff; }
    .bot-container .bot-card-body.danger {
      background-color: #ffe6e6; }
    .bot-container .bot-card-body .bot-current-zone {
      padding: 4px 0; }
    .bot-container .bot-card-body .bot-current-state {
      padding: 4px 0; }
    .bot-container .bot-card-body .bot-last-updated {
      padding: 4px 0; }
    .bot-container .bot-card-body .bot-controls {
      display: grid;
      grid-template-columns: 1fr 1fr;
      margin-top: 20px; }
      .bot-container .bot-card-body .bot-controls button {
        background-color: #ff9455;
        margin: 5px; }

.chat {
  margin: 12px 6px 6px 6px; }
  .chat .chat-history {
    background-color: #fff;
    height: 200px;
    margin-bottom: 10px;
    word-break: break-all;
    overflow: auto;
    padding: 8px;
    box-shadow: 0 0 1px #aaa; }
  .chat input {
    width: 80%; }
  .chat button {
    width: 20%; }

.header {
  align-items: center;
  background-color: #50a9e5;
  border-bottom: #aaa;
  display: flex;
  height: 60px; }
  .header a {
    letter-spacing: 1px;
    text-decoration: none;
    text-transform: uppercase; }
    .header a, .header a:visited, .header a:hover {
      color: #eee;
      text-decoration: none; }
    .header a.active, .header a.active:visited, .header a.active:hover, .header a.active:active {
      color: #fff;
      font-weight: bold; }
    .header a:not(last-of-type) {
      padding-right: 30px; }

.panel {
  border: 1px solid #eee;
  width: 400px; }

form .form-group {
  width: 100%; }
  form .form-group:not(:last-of-type) {
    margin-bottom: 20px; }
  form .form-group label {
    display: block;
    margin-bottom: 6px; }
  form .form-group input {
    display: block;
    width: 100%; }

.btn {
  background-color: #aaa;
  border: 0;
  color: #fff;
  cursor: pointer;
  display: inline-block;
  font-weight: bold;
  padding: 6px 12px;
  text-decoration: none; }
  .btn:focus {
    outline: 0; }

.btn-primary {
  background-color: #50a9e5; }

.btn-gray {
  background-color: #aaa; }

.error {
  background-color: #ff7b7b;
  color: #fff;
  font-size: 16px;
  font-weight: bold;
  padding: 20px; }

table {
  border-collapse: collapse;
  width: 100%; }

td {
  border-bottom: 1px solid #ddd;
  padding: .5rem; }

tr :last-of-type td {
  border-bottom: 0; }

@media screen and (max-width: 1800px) {
  .bot-container {
    grid-template-columns: 1fr 1fr 1fr 1fr; } }

@media screen and (max-width: 1400px) {
  .bot-container {
    grid-template-columns: 1fr 1fr 1fr; } }

@media screen and (max-width: 1000px) {
  .bot-container {
    grid-template-columns: 1fr 1fr; } }

@media screen and (max-width: 700px) {
  .bot-container {
    grid-template-columns: 1fr; } }

@media screen and (max-width: 440px) {
  .panel {
    width: 100%; } }

@media screen and (max-width: 400px) {
  .bot-controls {
    grid-template-columns: 1fr; } }
